
.content-followers{
  display: flex;
  justify-content: space-evenly;
}
.content-followers img{
  height: 50px;
  width: 50px;
  object-fit: cover;
  border-radius: 50%;
}
.user-details-section{
  margin-top: 7px;
  margin-left: -5px;
  cursor: pointer;
}
.user-details-section h4{
  font-size: 17.2px;
  margin-bottom: 3px;
}
.user-details-section p{
  color: #8A8888;
  font-size: 0.75rem;
}
.following-image{
  background: #1d1d1d;
    width: 40px;
    height: 38px;
    border-radius: 10px;
    margin-top: 8px;
    text-align: center;
    border: none;
}
.following-image:hover{
  box-shadow: 1px 1px 10px rgba(0,0,0,1.2);
  transition: .4s ease;
}
.following-image:focus{
  outline: none;
}
.following-image svg{
  height: 20px;
    width: 20px;
    /* margin: 0 auto; */
    margin-top: -2px;
    margin-left: 2px;
    color: white;
}

  
.tab-group {
  list-style: none;
  padding: 0;
  /* position: relative; */
  /* left: 22%; */
  /* margin: 0 auto; */
  display: flex;
  margin: 0 auto;
  place-content: center; 
    }
    .tab-group::after{
      content: "";
      display: table;
      clear: both;
    }
    .tab-group li p {
      display: block;
      text-decoration: none;
      padding: 10px;
      color: black;
      font-size: 16px;
      float: left;
      padding: 10px 35px;
      text-align: center;
      cursor: pointer;
      transition: .5s ease;
      border: 1px solid black;
      border-radius: 10px;
      margin: 0px 10px;
    }
    .tab-group li p:hover{
      background:#1D1D1D;
      color:white;
    }
    .tab-group .active p {
      background:#1D1D1D;
      color:white;
    }
  