.item-menu {
  margin: 20px 0px;
}
button.accordion-menu {
  width: 100%;
  background-color: white;
  border: none;
  outline: none;
  text-align: left;
  padding: 10px 13px;
  margin-top: 10px;
  font-size: 15px;
  font-weight: 600;
  color: #6c6c6c;
  cursor: pointer;
  transition: background-color 0.2s linear;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  grid-gap: 16px;
  border-radius: 25px;
  box-shadow: 2px 2px 18px rgb(0 0 0 / 10%);
}
button.accordion-menu:hover,
button.accordion-menu.is-open {
  box-shadow: 2px 2px 18px rgba(0, 0, 0, 0.1);
  transform: scale(1.008);
  transition: all 0.3s ease;
}
/* button.accordion-menu:focus{
      background: #06386C;
  } */
.left-menu-items {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  grid-gap: 20px;
}
.dish-img-menu {
  width: 80px;
  height: 80px;
  object-fit: cover;
  border-radius: 50%;
}
.menu-item-title h6 {
  font-weight: bold;
  margin-bottom: 2px;
  color: #06386c;
}
.menu-item-title h6:focus {
  background: white;
  color: white;
}
.menu-item-title span {
  font-size: 12px;
  font-weight: lighter;
  color: #06386c;
}
.right-menu-items {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  grid-gap: 20px;
}
.right-menu-items h6 {
  margin: 0 auto;
  font-size: 14px;
  color: #06386c;
}

/* Switch */

.switch {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 30px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 22px;
  width: 22px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #5ddc95;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(20px);
  -ms-transform: translateX(20px);
  transform: translateX(20px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

/* menu-content */
.accordion-content-menu {
  background-color: white;
  border-left: 1px solid whitesmoke;
  border-right: 1px solid whitesmoke;
  margin-bottom: 10px;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-in-out;
  border-radius: 25px;
}
.accordion-content-menu-hide {
  background-color: white;
  border-left: 1px solid whitesmoke;
  border-right: 1px solid whitesmoke;
  padding-left: 8px;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-in-out;
  display: none;
}
.align-content-menu {
  margin: 20px 0px;
  padding-bottom: 10px;
}
.add-category-btn {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.blue-plus-btn {
  color: #06386c;
  background: #ffffff00;
  width: 36px;
  height: 100%;
  font-size: 18px;
  border: none;
  outline: none;
}
.add-category-btn span {
  color: #06386c;
  font-size: 13px;
  font-weight: bold;
}

.form-ET {
  max-width: 230px;
  width: 100%;
  margin-left: 15px;
  margin-bottom: 5px;
  min-width: fit-content;
  border-radius: 15px;
  box-shadow: 0px 4px 6px rgb(0 0 0 / 10%);
}
.form-ET .form-input-ET {
  display: flex;
  align-items: center;
  height: 36px;
}
.form-ET .form-input-ET input {
  flex-grow: 1;
  padding: 20px 16px;
  height: 100%;
  border: 1px solid grey;
  background: #ffffff;
  border-radius: 5px;
  outline: none;
  width: 100%;
  font-size: 13px;
}
.form-ET .form-input-ET input::placeholder {
  color: #a19e9e;
}
.form-ET .form-input-ET button {
  margin-left: -5px;
  width: 36px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
  color: black;
  font-size: 18px;
  border: 1px solid grey;
  outline: none;
  border-radius: 0 5px 5px 0;
  padding: 20px;
}

