


#more {
  display: none;
}
#myBtn {
  text-decoration: none;
  color: #636363;
  font-size: 0.84rem;
  cursor: pointer;
}
#myBtn:hover {
  color: #016af3;
}
.section-cuisine {
  display: flex;
  margin: 0 0 15px;
}
.image-cuisine {
  flex-shrink: 0;
}
.image-cuisine img {
  width: 32px;
  margin-right: 15px;
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  border: 0;
}
.div-text-cuisine h4 {
  font-size: 13.5px;
  color: #ac8e48;
  padding: 2px 0;
  margin-bottom: -4px;
  font-weight: 700;
}
.div-text-cuisine a {
  text-decoration: none;
  color: #636363;
  font-size: 13px;
  font-weight: 500;
}
.div-text-cuisine a:hover {
  text-decoration: none;
}
.div-text-cuisine a::after {
  content: ",";
}
.div-text-cuisine a:last-child::after {
  content: "";
}
.ul-facilities {
  padding: 25px 0 0 0;
  flex-wrap: wrap;
  display: flex;
  list-style: none;
  margin-bottom: 0px;
}
.ul-facilities li {
  width: 33%;
  padding: 0 10px 10px 0;
  color: #757575;
}
.ul-facilities li img {
  width: 22px;
  margin-right: 10px;
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  border: 0;
}
.ul-facilities li a {
  text-decoration: none;
  color: #636363;
  font-size: 13px;
  font-weight: 500;
}


@media screen and (min-width: 320px) {
  .border-about-section {
    background-color: #fff;
    margin: 0px 12px;
    margin-bottom: 12px;
  }
  .content-about-section {
    padding: 20px 15px;
  }
  .content-about-section h6 {
    font-weight: 700;
    font-size: 1.2rem;
  }
  .content-about-section p {
    font-size: 0.84rem;
    color: #000;
  }
}

@media screen and (min-width: 992px) {
  .border-about-section {
    background-color: #fff;
    margin: 0px;
    margin-bottom: 12px;
  }
  .content-about-section {
    padding: 20px;
  }
  .content-about-section h6 {
    font-weight: 700;
    font-size: 1.2rem;
  }
  .content-about-section p {
    font-size: 0.84rem;
    color: #000;
  }
}