.header-PS {
  display: block;
  padding: 0px 26px;
}



.down-arrow-PS {
  position: absolute;
  pointer-events: none;
  right: 4px;
  display: inline-block;
  height: 1.5rem;
  width: 1.5rem;
  min-width: 1.5rem;
  font-size: 0;
  line-height: 1;
}
.down-arrow-PS img {
  width: 12px;
  height: 12px;
  margin-top: 6px;
}
.dropdown-PS {
  position: relative;
  display: flex;
  align-items: center;
}
.dropdown-PS select {
  font-size: 13px;
  color: #474747cc;
  width: 100%;
  border: 1px solid rgb(6 56 108 / 50%);
  border-radius: 4px;
  height: 100%;
  display: block;
  resize: vertical;
  box-sizing: border-box;
  min-height: 38px;
  padding: 0px 10px;
  -webkit-appearance: none;
  text-transform: none;
  font-family: inherit;
  line-height: 1.15;
  margin: 0;
}
.dropdown-PS select:focus{
  outline: none;
}
.dropdown-PS .Darrow {
  position: absolute;
  pointer-events: none;
  right: 4px;
  display: inline-block;
  height: 1.5rem;
  width: 1.5rem;
  min-width: 1.5rem;
  font-size: 0;
  line-height: 1;
}
.dropdown-PS .Darrow img {
  width: 12px;
  height: 12px;
  margin-top: 6px;
}
.radio-PS {
  margin-bottom: 10px;
}
.radio-PS label {
  font-size: 12px;
  color: #06386c;
}



.select-cuisine-btn {
  background: #06386c;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 11px;
  font-size: 13px;
  width: 100%;
}
.select-cuisine-btn:hover {
  box-shadow: 2px 2px 18px rgba(0, 0, 0, 0.1);
  transform: scale(1.008);
  transition: all 0.2s ease;
}
.dropdown-PS .upload-img-PS {
  background: #06386c;
  border-radius: 4px;
  position: absolute;
  pointer-events: none;
  display: inline-block;
  height: 1.5rem;
  width: 1.5rem;
  min-width: 1.5rem;
  font-size: 0;
  line-height: 1;
  right: 0px;
  width: 50px;
  height: 37px;
  text-align: center;
  margin: 0 auto;
  display: grid;
  align-content: center;
}
.dropdown-PS .upload-img-PS h6 {
  color: white;
  margin: 0 auto;
  font-size: 13px;
}

.green-btn-S {
  width: 114px;
  /* height: 46px; */
  background: #5ddc95;
  color: white;
  border: white;
  border-radius: 17px;
  padding: 10px 40px;
  font-size: 14px;
  transition: background-color 0.3s linear;
  font-family: Raleway;
}
.green-btn-S:hover {
  background: #43cb7f;
  box-shadow: 2px 2px 18px rgba(0, 0, 0, 0.1);
  transform: scale(1.008);
  transition: all 0.2s ease;
}



/*checkbox */
.content-CB-PS {
  display: block;
  position: relative;
  padding-left: 24px;
  margin-top: 6px;
  margin-left: 20px;
  margin-bottom: 8px;
  cursor: pointer;
  font-size: 14px;
  align-items: center;
  justify-content: center;
  align-content: center;
  justify-items: center;
}
.label-CB-PS {
  font-size: 12px;
  margin-bottom: -24px;
  color: #06386c;
}
.content-CB-PS input[type="radio"] {
  visibility: hidden;
}
.checkbox-CB-PS {
  position: absolute;
  top: 0;
  left: 0;
  height: 12px;
  width: 12px;
  background-color: white;
  border-radius: 3px;
  border: 1px solid #06386c;
}
.content-CB-PS input:active ~ .checkbox-CB-PS {
  background-color: white;
}
.content-CB-PS input:checked ~ .checkbox-CB-PS {
  background-color: #0083c7;
}
.checkbox-CB-PS:after {
  content: "";
  position: absolute;
  display: none;
}
.content-CB-PS input:checked ~ .checkbox-CB-PS:after {
  display: block;
}
.content-CB-PS .checkbox-CB-PS:after {
  left: 4px;
  bottom: 1px;
  width: 4px;
  height: 12px;
  border: solid white;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.dropdown-PS .detect-btn {
