.link a {
  text-decoration: none;
}
.box-restaurent {
  width: 240px;
  /* box-shadow: 0px 0px 6px rgb(0 0 0 / 20%); */
  border-radius: 30px;
  overflow: hidden;
  margin: 10px;
  margin-top: 23px;
  background-color: #fff;
  cursor: pointer;
}
.box-restaurent a {
  text-decoration: none;
}
.box-restaurent:hover {
  box-shadow: 2px 2px 18px rgba(0, 0, 0, 0.1);
  transform: scale(1.008);
  transition: all 0.3s ease;
}
.img-restaurent {
  height: 180px;
  position: relative;
}
.img-restaurent img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  box-sizing: border-box;
  border-radius: 4px;
}
.detail-box-restaurent {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  padding-right: 12px;
  box-sizing: border-box;
  flex-wrap: wrap;
}
.restaurent-details {
  display: flex;
  flex-direction: column;
}
.restaurent-details a {
  color: #222222;
  margin: 5px 0px;
  font-weight: 700;
  letter-spacing: 0.5px;
  padding-right: 8px;
  text-decoration: none;
  font-size: 20px;
  margin-bottom: 1px;
  width: 170px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.restaurent-details a:hover {
  text-decoration: none;
  color: black;
}

.restaurent-distance {
  color: #333333;
  font-weight: 600;
  font-size: 11px;
  letter-spacing: 0.5px;
  margin-top: 0px;
  background-color: #c8dec9;
  padding: 0px 5px;
  padding-top: px;
  border-radius: 20px;
  text-decoration: none;
  padding-top: 1px;
  padding-bottom: 1px;
  min-inline-size: fit-content;
}
.restaurent-distance:hover {
  text-decoration: none;
  color: black;
}
.restaurent_item .rate-for-ratings-section {
  margin: 0;
}
.restaurent_item .rate-for-ratings-section span {
  line-height: 1;
}
.restaurent_item .rate-for-ratings-section > span {
  position: absolute;
  right: -75px;
  top: 7px;
  font-size: 13px;
  color: black;
  font-weight: 500;
}
.heart-icon {
  position: absolute;
  right: 0%;
  top: 13%;
  transform: translate(-50%, -50%);
  width: 9%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.rating a {
  text-decoration: none;
  cursor: text;
}
.rating a:hover {
  color: white;
  cursor: text;
}
.restaurent-rating {
  width: 45px;
  height: 30px;
  display: flex;
  background-color: #439548;
  color: #ffffff;
  font-weight: 400;
  letter-spacing: 1px;
  border-radius: 6px;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  cursor: text;
  position: absolute;
  top: 10px;
  right: 10px;
}
.restaurent-location {
  font-weight: 500;
  color: #949494;
  font-size: 12px;
}
.restaurent-discount {
  font-weight: bolder;
  color: #439548;
  font-size: 14px;
  padding-top: 10px;
  border-top: 1px solid #94949430;
  width: 100%;
  display: block;
  margin-top: 10px;
}
.ac-footer .ac-icon {
  position: absolute;
  /* left: 0; */
  right: 0;
  top: 0;
  margin-top: 5px;
  margin-right: 10px;
  width: 20px;
  height: 30px;
}
.ac-footer .fa-heart-o {
  position: absolute;
  top: 14px;
  right: 17px;
  color: white;
  font-size: 20px;
}
.ac-footer .fa-heart-o:hover::before {
  content: "\f004";
  color: #439548;
}
.ac-footer .fa-heart-o:hover {
  border: 1px solid #439548 !important;
}
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(251, 54, 110, 0.4);
  }
  70% {
    box-shadow: 0 0 0 30px rgba(251, 54, 110, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(251, 54, 110, 0);
  }
}
@keyframes pulseHeart {
  0% {
    transform: scale(1.3);
  }
  70% {
    transform: scale(1.15);
  }
  100% {
    transform: scale(1);
  }
}
.ac-icon-love-dark {
  display: block;
  position: relative;
}
.ac-icon-love-dark:before {
  content: "";
  display: block;
  width: 8px;
  height: 8px;
  position: absolute;
  top: 45%;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);
  right: 0;
  z-index: -1;
  border-radius: 100%;
}
.ac-footer-container:hover .ac-icon-love-dark {
  animation: pulseHeart 1s ease infinite;
}
.ac-footer-container:hover .ac-icon-love-dark:before {
  animation: pulse 1s ease infinite !important;
}
.ac-icon-triangle {
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 5px solid #788795;
  position: relative;
  display: inline-block;
  margin-left: 8px;
}
.ac-footer .ac-footer-brand:hover .ac-icon-love-dark {
  background: url("https://community.algolia.com/places/images/svg/icon-heart-light-fc92b1e0.svg")
    no-repeat center center/contain;
}

@media screen and (min-width: 320px) {
  .box-restaurent {
    width: 95%;
    /* box-shadow: 0px 0px 6px rgb(0 0 0 / 20%); */
    border-radius: 10px;
    overflow: hidden;
    margin: 0px;
    margin-bottom: 10px;
    margin-top: 23px;
    background-color: #fff;
    cursor: pointer;
  }
}
@media screen and (min-width: 992px) {
  .box-restaurent {
    width: 95%;
    /* box-shadow: 0px 0px 6px rgb(0 0 0 / 20%); */
    border-radius: 10px;
    overflow: hidden;
    margin: 10px;
    margin-top: 23px;
    background-color: #fff;
    cursor: pointer;
  }
}
